<template>
  <div class="stockManage">

    <div class="stockManage-title">
      <span class="stockManage-title-slashAfter">库存管理</span>
    </div>

    <div class="stockManage-content">
      <el-tabs type="border-card" stretch @tab-click="changeTab">
        <el-tab-pane label="轿车" value="car">
          <div class="stockManage-content-card" v-for="(item, index) in cardList" :key="index">
            <img :src="item.image" alt="">
            <h4>{{item.name}}</h4>
            <p>库存量:<span class="stockManage-content-span">{{item.inventory}}</span>辆</p>
            <el-button type="warning" round plain icon="el-icon-edit-outline" @click="openDialog(item)">库存变更</el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="SUV" value="suv">
          <div class="stockManage-content-card" v-for="(item, index) in cardList" :key="index">
            <img :src="item.image" alt="">
            <h4>{{item.name}}</h4>
            <p>库存量:<span class="stockManage-content-span">{{item.inventory}}</span>辆</p>
            <el-button type="warning" round plain icon="el-icon-edit-outline" @click="openDialog(item)">库存变更</el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="MPV" value="mpv">
          <div class="stockManage-content-card" v-for="(item, index) in cardList" :key="index">
            <img :src="item.image" alt="">
            <h5>{{item.name}}</h5>
            <p>库存量:<span class="stockManage-content-span">{{item.inventory}}</span>辆</p>
            <el-button type="warning" round plain icon="el-icon-edit-outline" @click="openDialog(item)">库存变更</el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="跑车" value="sportscar">
          <div class="stockManage-content-card" v-for="(item, index) in cardList" :key="index">
            <img :src="item.image" alt="">
            <h4>{{item.name}}</h4>
            <p>库存量:<span class="stockManage-content-span">{{item.inventory}}</span>辆</p>
            <el-button type="warning" round plain icon="el-icon-edit-outline" @click="openDialog(item)">库存变更</el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="皮卡" value="pickup">
          <div class="stockManage-content-card" v-for="(item, index) in cardList" :key="index">
            <img :src="item.image" alt="">
            <h4>{{item.name}}</h4>
            <p>库存量:<span class="stockManage-content-span">{{item.inventory}}</span>辆</p>
            <el-button type="warning" round plain icon="el-icon-edit-outline" @click="openDialog(item)">库存变更</el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="微面" value="minivan">
          <div class="stockManage-content-card" v-for="(item, index) in cardList" :key="index">
            <img :src="item.image" alt="">
            <h4>{{item.name}}</h4>
            <p>库存量:<span class="stockManage-content-span">{{item.inventory}}</span>辆</p>
            <el-button type="warning" round plain icon="el-icon-edit-outline" @click="openDialog(item)">库存变更</el-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="微卡" value="miniup">
          <div class="stockManage-content-noStock" v-if="cardList.length == 0">暂无数据</div>
          <div class="stockManage-content-card" v-for="(item, index) in cardList" :key="index">
            <img :src="item.image" alt="">
            <h4>{{item.name}}</h4>
            <p>库存量:<span class="stockManage-content-span">{{item.inventory}}</span>辆</p>
            <el-button type="warning" round plain icon="el-icon-edit-outline" @click="openDialog(item)">库存变更</el-button>
          </div>
          </el-tab-pane>
        <el-tab-pane label="轻客" value="lightbus"><div class="stockManage-content-card" v-for="(item, index) in cardList" :key="index">
            <img :src="item.image" alt="">
            <h4>{{item.name}}</h4>
            <p>库存量:<span class="stockManage-content-span">{{item.inventory}}</span>辆</p>
            <el-button type="warning" round plain icon="el-icon-edit-outline" @click="openDialog(item)">库存变更</el-button>
          </div></el-tab-pane>
      </el-tabs>
    </div>

    <!-- 库存变更 -->
    <div class="stockManage-dialog">
      <el-dialog
        title="库存变更"
        :visible.sync="stockDialog"
        width="30%"
        center
        :before-close="closeDialog"
      >
        <div class="stockManage-dialog-form">
          <el-form
            :model="stockForm"
            status-icon
            :rules="rules"
            ref="stockForm"
            label-width="100px"
          >
            <el-row :gutter="20">
              <el-col :span="24">
                <el-form-item label="操作">
                  <el-switch
                    style="display: block"
                    v-model="change"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    active-text="增加"
                    inactive-text="减少">
                  </el-switch>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="变更数量" prop="number">
                  <el-input :controls="false" v-model.number="stockForm.number"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="closeDialog">取 消</el-button>
            <el-button type="primary" @click="isConfirm">提 交</el-button>
        </span>
      </el-dialog>
    </div>

  </div>
</template>

<script>
import { carStock } from "../api/";

export default {
  name: "StockManage",
  data () {
    var judgingQuantity = (rule, value, callback) => {
      if(!this.change){
        if (value > this.inventory) {
          callback(new Error('减少量不能多于库存量'));
        } else {
          callback();
        }
      }
      else {
          callback();
        }
    };
    return {
      cardList: [],
      tabList: [],
      stockDialog: false,
      change: true,
      stockForm: {
        number: ""
      },
      inventory: 0,
      itemInfo: {},
      rules: {
        number: [
          { required: true, message: "请输入变更数量", trigger: "blur" },
          { required: true, validator: judgingQuantity, trigger: "blur" }
        ]
      },
    }
  },
  created() {
    // 默认展示所有轿车的库存量
    carStock
      .then(res => {
        this.tabList = res.data.data;
        this.cardList = this.tabList.filter(item => item.category == "car")
      })
      .catch(function(error) {
        return error;
      });
  },
  methods: {
    // 菜单切换
    changeTab (val) {
     this.cardList = this.tabList.filter(item => item.category == val.$attrs.value) 
    },
    // 点击库存变更，打开编辑对话框
    openDialog (val) {
      this.stockDialog = !this.stockDialog
      this.itemInfo = val
      this.inventory = val.inventory
    },
    closeDialog () {
      this.stockDialog = !this.stockDialog
    },
    // 确认提交修改吗
    isConfirm() {
      this.$refs["stockForm"].validate(valid => {
        if (valid) {
          this.$confirm("确认提交吗？")
            .then(_ => {
              if( !this.change ){
                this.stockForm.number = -this.stockForm.number;
              }
              this.carStockChg()
              this.closeDialog()
              this.$message({
                type: "success",
                message: "变更成功!"
              });
              this.stockForm.number = ""
            })
            .catch(_ => {
              this.closeDialog();
            });
        } else return false;
      });
    },
    carStockChg() {
      this.tabList = this.tabList.map(item => {
        if(item.name == this.itemInfo.name) {
          item.inventory += this.stockForm.number;
        }
        return item
      })
      this.cardList = this.tabList.filter(item => item.category == "car")
    }
  },
}
</script>

<style scoped>
.stockManage {
  padding: 20px;
  padding-top: 10px;
  overflow-y: scroll;
}
/* 标题 */
.stockManage-title {
  padding-bottom: 10px;
}
.stockManage-title-slashAfter {
  font-size: 16px;
  font-weight: bold;
  color: #606266;
}
/* 内容 */
.stockManage-content-card{
  display: inline-block;
  margin: 5px 4%;
  width: 15%;
  height: auto;
  border: 1px solid #ebeef5;
  text-align: center;
}
.el-tabs--border-card>.el-tabs__content{
  padding-top: 5px;
}
.stockManage-content-card img{
  width: 100%
}
.stockManage-content-card h4,.stockManage-content-card p,.stockManage-content-card .el-button{
  margin: 5px 0;
}
.stockManage-content-card p{
  font-size: 12px;
}
.stockManage-content-span{
  font-weight: bold;
}
.el-switch{
  padding: 10px 0;
}
.stockManage-content-noStock{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  text-align: center;
  color: rgb(144, 147, 153)
}
  
</style>


